@import url("https://fonts.googleapis.com/css?family=Roboto:400,500");

.playList{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 5px;
  padding-top: 5px;  
  color: #560103 ;
  font-size: 14px;
}

 #playlist {
  max-width: 200px;
  border :0;
  padding-left: 5px;
  font-size: 14px;
  background-color: transparent;
}

#playlist ::after{
  border :0;
}

.switch {
  position: relative;
 display: flex;
  width: 50px;
  height: 26px;
  align-items: center;
  right: 14px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #fff;
}



input:checked + .slider:before {
  -webkit-transform: translateX(22px);
  -ms-transform: translateX(22px);
  transform: translateX(22px);
  background-color: #560103;
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
