.items-container {
 display : flex; 
 flex-direction: row;
 position: relative;
 flex-flow: wrap;
  justify-content: space-around;
  align-items: center;
  bottom: 0;
  height: 80vh;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
}

.item-container {
  align-items: center;
  display: flex;
  position: relative;
  max-width: 370px;
  justify-content: space-around;
  padding: 10px 20px ;
  cursor: pointer;
}

.loadMore{
  align-items: top;
  display: flex;
  position: relative;
  border:0;
  height: 70%;
  min-width: 100%;
 cursor: pointer;
  background-color:transparent;
  color: #560103;
  font-weight:bold;
  justify-content: space-around;
  padding: 20px 20px ;
  
}

.number {
  align-self: center;
  letter-spacing: 0;
  margin-bottom: 1px;
  min-height: 18px;
  width: 13px;
  color:gray;
}

.thumbnail {
  background-color: white;
  border-radius: 11px;
  height: 97px;
  margin-left: 14px;
  width: 171px;
}

img{
  border: 0;
}

.description {
  padding-left: 20px;
  font-size: auto ;
}
